$assets_dir: "/wp-content/plugins/product-cards-customiser-for-woocommerce/public/assets/img/";

@function setUrl($file) {
  @return url(#{$assets_dir + $file});
}

.product-cards-customiser {
    width: calc(100% - 20px);

    .product-cards-customiser-inner {
        max-width: 1000px;

        h1 {
            margin-top: 30px;
        }

        .templates {
            margin-top: 30px;

            .templates-inner {
                display: grid;
                gap: 15px;
                grid-template-columns: 1fr 1fr 1fr 1fr;

                @media screen and (max-width: 992px) {
                    grid-template-columns: 1fr 1fr 1fr;
                }

                @media screen and (max-width: 768px) {
                    grid-template-columns: 1fr 1fr;
                }

                @media screen and (max-width: 450px) {
                    grid-template-columns: 1fr;
                }

                .product-card-design-item {
                    aspect-ratio: 1 / 1;
                    background: #fff;
                    border: 10px solid #fff;
                    border-bottom: 0;
                    border-radius: 30px;
                    box-shadow: 3px 3px 2px #ddd;
                    cursor: pointer;
                    display: flex;
                    flex-direction: column;
                    overflow: hidden;
                    text-align: center;
                    transition: .3s;
                    -webkit-user-select: none;
                    -moz-user-select: none;
                    user-select: none;

                    &:hover {
                        transform: translate(3px, 3px);
                        box-shadow: none;
                        transition: .3s;
                    }

                    .image {
                        align-items: center;
                        background: #d3d3d3;
                        background-position: 50%;
                        background-repeat: no-repeat;
                        background-size: 35%;
                        border-radius: 30px;
                        display: flex;
                        height: 60%;
                        justify-content: center;
                        padding: 10% 0;
                        width: 100%;
                        background-image: setUrl("placeholder-image.svg");
                    }

                    .name {
                        display: block;
                        font-size: 18px;
                        font-weight: 400;
                        margin-top: auto;
                        margin-bottom: auto;
                    }
                }
            }
        }

        .action-links {
            margin-top: 30px;
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr 1fr;

            #create-new-template {
                padding: 10px 20px;
                background: white;
                font-size: 16px;
                border-radius: 5px;
                text-decoration: none;
                display: block;
                width: fit-content;
                color: black;
                box-shadow: 3px 3px 2px #dddddd;
                transition: .3s;
                text-align: center;
                white-space: nowrap;

                &:hover {
                    transform: translate(3px, 3px);
                    box-shadow: none;
                    transition: .3s;
                }
            }
        }
    }
}