.product-cards-customiser {
  width: calc(100% - 20px);
}
.product-cards-customiser .product-cards-customiser-inner {
  max-width: 1000px;
}
.product-cards-customiser .product-cards-customiser-inner h1 {
  margin-top: 30px;
}
.product-cards-customiser .product-cards-customiser-inner .templates {
  margin-top: 30px;
}
.product-cards-customiser .product-cards-customiser-inner .templates .templates-inner {
  display: grid;
  gap: 15px;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}
@media screen and (max-width: 992px) {
  .product-cards-customiser .product-cards-customiser-inner .templates .templates-inner {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
@media screen and (max-width: 768px) {
  .product-cards-customiser .product-cards-customiser-inner .templates .templates-inner {
    grid-template-columns: 1fr 1fr;
  }
}
@media screen and (max-width: 450px) {
  .product-cards-customiser .product-cards-customiser-inner .templates .templates-inner {
    grid-template-columns: 1fr;
  }
}
.product-cards-customiser .product-cards-customiser-inner .templates .templates-inner .product-card-design-item {
  aspect-ratio: 1/1;
  background: #fff;
  border: 10px solid #fff;
  border-bottom: 0;
  border-radius: 30px;
  box-shadow: 3px 3px 2px #ddd;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  text-align: center;
  transition: 0.3s;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}
.product-cards-customiser .product-cards-customiser-inner .templates .templates-inner .product-card-design-item:hover {
  transform: translate(3px, 3px);
  box-shadow: none;
  transition: 0.3s;
}
.product-cards-customiser .product-cards-customiser-inner .templates .templates-inner .product-card-design-item .image {
  align-items: center;
  background: #d3d3d3;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: 35%;
  border-radius: 30px;
  display: flex;
  height: 60%;
  justify-content: center;
  padding: 10% 0;
  width: 100%;
  background-image: url(/wp-content/plugins/product-cards-customiser-for-woocommerce/public/assets/img/placeholder-image.svg);
}
.product-cards-customiser .product-cards-customiser-inner .templates .templates-inner .product-card-design-item .name {
  display: block;
  font-size: 18px;
  font-weight: 400;
  margin-top: auto;
  margin-bottom: auto;
}
.product-cards-customiser .product-cards-customiser-inner .action-links {
  margin-top: 30px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
}
.product-cards-customiser .product-cards-customiser-inner .action-links #create-new-template {
  padding: 10px 20px;
  background: white;
  font-size: 16px;
  border-radius: 5px;
  text-decoration: none;
  display: block;
  width: fit-content;
  color: black;
  box-shadow: 3px 3px 2px #dddddd;
  transition: 0.3s;
  text-align: center;
  white-space: nowrap;
}
.product-cards-customiser .product-cards-customiser-inner .action-links #create-new-template:hover {
  transform: translate(3px, 3px);
  box-shadow: none;
  transition: 0.3s;
}